footer {
   background-color: #0c0c0c;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   padding: 50px 0;
}

@media screen and (max-width: 712px) {
   footer {
      flex-direction: column;
   }

   footer ul {
      display: flex;
      flex-wrap: wrap;
   }
}